<template>
  <nav class="navbar">
    <h1 class="navbar-title">Product Manager</h1>
    <div class="navbar-links">
      <router-link to="/">Cancel</router-link>
      <button @click="addproduct" class="btn">Save</button>
    </div>
  </nav>
  <h2>Add product</h2>
  <div class="add-product-container">
    <!-- Common Fields -->
    <div class="form">
      <div class="form-group">
        <label>SKU:</label>
        <input type="text" v-model="product.sku" placeholder="Enter SKU" />
      </div>

      <div class="form-group">
        <label>Name:</label>
        <input
          type="text"
          v-model="product.name"
          placeholder="Enter Product Name"
        />
      </div>

      <div class="form-group">
        <label>Price:</label>
        <textarea
          v-model="product.price"
          placeholder="Enter Product price"
        ></textarea>
      </div>

      <!-- Category Selection -->
      <div class="form-group">
        <label>Category:</label>
        <select v-model="product.categoryClass" class="selector">
          <option value="Book">Book</option>
          <option value="DVD">DVD</option>
          <option value="Furniture">Furniture</option>
        </select>
      </div>

      <!-- Conditional Fields for Order -->
      <div v-if="product.categoryClass === 'Furniture'" class="form-group">
        <label>Height</label>
        <input type="text" v-model="additionalInfo.height" />
        <label>Width</label>
        <input type="text" v-model="additionalInfo.width" />
        <label>Length</label>
        <input type="text" v-model="additionalInfo.length" />
      </div>

      <!-- Conditional Fields for Book -->
      <div v-if="product.categoryClass === 'Book'" class="form-group">
        <label>Weight:</label>
        <input type="text" v-model="additionalInfo.weight" />
      </div>

      <!-- Conditional Fields for Course -->
      <div v-if="product.categoryClass === 'DVD'" class="form-group">
        <label>Size:</label>
        <input type="text" v-model="additionalInfo.size" />
      </div>
    </div>

    <transition name="fade">
      <div v-if="showErrorPopup" class="popup">
        <div class="popup-content">
          <h3>{{ errorMessage }}</h3>
          <button @click="closeErrorPopup" class="btn">Close</button>
        </div>
      </div>
    </transition>
  </div>
  <footercommon></footercommon>
</template>
<script>
import axios from "axios";
import footercommon from "@/components/footer-common.vue";
export default {
  data() {
    return {
      showPopup: false, // For showing the success popup
      showErrorPopup: false, // For showing the error popup
      errorMessage: "", // Error message to display in popup
      product: {
        sku: "",
        name: "",
        price: "",
        categoryClass: "", // Default category
      },
      additionalInfo: {
        size: "", // for DVD
        weight: "", // Specific for Book
        height: "", // Specific for furniture
        width: "", // Specific for furniture
        length: "", // Specific for furniture
      },
    };
  },
  components: { footercommon },
  methods: {
    validateFields() {
      // Check if required fields are empty
      if (
        !this.product.sku ||
        !this.product.name ||
        !this.product.price ||
        !this.product.categoryClass
      ) {
        this.errorMessage = "All fields are required.";
        return false;
      }

      // Price validation (must be a number)
      if (isNaN(this.product.price)) {
        this.errorMessage =
          "Please, provide the data of indicated type (price must be a number).";
        return false;
      }

      // Furniture category validation
      if (this.product.categoryClass === "Furniture") {
        if (
          !this.additionalInfo.height ||
          !this.additionalInfo.width ||
          !this.additionalInfo.length
        ) {
          this.errorMessage =
            "All dimensions are required for Furniture category.";
          return false;
        }

        // Check if dimensions are numbers
        if (
          isNaN(this.additionalInfo.height) ||
          isNaN(this.additionalInfo.width) ||
          isNaN(this.additionalInfo.length)
        ) {
          this.errorMessage =
            "Please, provide the data of indicated type (dimensions must be numbers).";
          return false;
        }
      }

      // Book category validation
      if (this.product.categoryClass === "Book") {
        if (!this.additionalInfo.weight) {
          this.errorMessage = "Weight is required for Book category.";
          return false;
        }

        // Check if weight is a number
        if (isNaN(this.additionalInfo.weight)) {
          this.errorMessage =
            "Please, provide the data of indicated type (weight must be a number).";
          return false;
        }
      }

      // DVD category validation
      if (this.product.categoryClass === "DVD") {
        if (!this.additionalInfo.size) {
          this.errorMessage = "Size is required for DVD category.";
          return false;
        }

        // Check if size is a number
        if (isNaN(this.additionalInfo.size)) {
          this.errorMessage =
            "Please, provide the data of indicated type (size must be a number).";
          return false;
        }
      }

      // If everything passes
      return true;
    },
    addproduct() {
      if (!this.validateFields()) {
        this.showErrorPopup = true;
        // console.log(this.product.categoryClass + this.errorMessage);
        return;
      }

      // Construct data based on category
      let dataToSend = {
        sku: this.product.sku,
        name: this.product.name,
        price: this.product.price,
        categoryClass: this.product.categoryClass,
        additionalInfo: {},
      };

      // Add category-specific data
      if (this.product.categoryClass === "Furniture") {
        dataToSend.additionalInfo.height = this.additionalInfo.height;
        dataToSend.additionalInfo.width = this.additionalInfo.width;
        dataToSend.additionalInfo.length = this.additionalInfo.length;
      } else if (this.product.categoryClass === "Book") {
        dataToSend.additionalInfo.weight = this.additionalInfo.weight;
      } else if (this.product.categoryClass === "DVD") {
        dataToSend.additionalInfo.size = this.additionalInfo.size;
      }

      axios
        .post("http://localhost/ToDoApplication/add-product", dataToSend)
        .then((response) => {
          if (response.data.success) {
            this.$router.push("/");
          } else if (response.data.error.includes("SKU already exists")) {
            // Specific error for SKU validation
            this.errorMessage =
              "The SKU already exists. Please use a unique SKU.";
            this.showErrorPopup = true;
          } else {
            this.errorMessage = "Failed to add product: " + response.data.error;
            this.showErrorPopup = true;
          }
        })
        .catch((error) => {
          this.errorMessage = "Error adding product: " + error;
          this.showErrorPopup = true;
        });
    },

    closePopup() {
      this.showPopup = false;
    },
    closeErrorPopup() {
      this.showErrorPopup = false;
    },
  },
};
</script>
