<template>
  <div class="task-list">
    <nav class="navbar">
      <h1 class="navbar-title">Task Manager</h1>
      <div class="navbar-links">
        <div class="button-group">
          <button @click="deleteTasks" :disabled="selectedTasks.length === 0">
            MASS DELETE
          </button>
        </div>
        <router-link to="/add-task">ADD</router-link>
      </div>
    </nav>

    <ul class="tasks-container">
      <li
        v-for="task in tasks"
        :key="task.id"
        class="task-item"
        @mouseover="hover = true"
        @mouseleave="hover = false"
      >
        <input
          type="checkbox"
          :value="task.id"
          @click="toggleTaskSelection(task.id, $event)"
          :checked="selectedTasks.includes(task.id)"
          class="delete-checkbox"
        />
        <div class="task-content">
          <h3 class="task-title">{{ task.sku }}</h3>
          <div class="info">
            <span>
              <strong>{{ task.name }}</strong></span
            >
            <div v-if="task.categoryClass === 'book'">
              <p>Weight: {{ task.additionalInfo.weight }} KG</p>
            </div>
            <div v-if="task.categoryClass === 'dvd'">
              <p>Size: {{ task.additionalInfo.size }} MB</p>
            </div>
            <div v-if="task.categoryClass === 'furniture'">
              <p>
                Dimensions: {{ task.additionalInfo.height }} x
                {{ task.additionalInfo.width }} x
                {{ task.additionalInfo.length }}
              </p>
            </div>
          </div>
        </div>
      </li>
    </ul>
  </div>
  <footercommon></footercommon>
</template>

<script>
import footercommon from "@/components/footer-common.vue";
import axios from "axios";

export default {
  data() {
    return {
      tasks: [],
      selectedTasks: [],
      hover: false,
    };
  },
  components: { footercommon },
  mounted() {
    this.fetchTasks();
  },

  methods: {
    async fetchTasks() {
      try {
        const response = await axios.get("http://localhost/ToDoApplication");
        this.tasks = response.data; // Assuming the response is an array of tasks
      } catch (error) {
        alert("Error fetching tasks:", error);
      }
    },
    async deleteTasks() {
      if (this.selectedTasks.length === 0) {
        alert("Please select at least one task to delete.");
        return;
      }

      const payload = {
        ids: [...this.selectedTasks],
      };

      try {
        const response = await fetch(
          `http://localhost/ToDoApplication/delete-tasks`,
          {
            method: "DELETE",
            headers: {
              "Content-Type": "application/json",
            },
            body: JSON.stringify(payload),
          }
        );

        if (!response.ok) {
          const errorData = await response.json();
          throw new Error(errorData.message || "Error deleting tasks.");
        }

        const data = await response.json();

        if (data.success) {
          this.tasks = this.tasks.filter(
            (task) => !this.selectedTasks.includes(task.id)
          );
          this.selectedTasks = [];
        } else {
          alert(data.message || "Error deleting tasks.");
        }
      } catch (error) {
        // console.error("Error deleting tasks:", error);
        alert("An error occurred: " + error.message);
      }
    },

    toggleTaskSelection(id, event) {
      if (event.target.checked) {
        this.selectedTasks.push(id);
        // console.log("Added Task ID:", id);
      } else {
        this.selectedTasks = this.selectedTasks.filter(
          (selectedId) => selectedId !== id
        );
        // console.log("Removed Task ID:", id);
      }
      // console.log("Current Selected Tasks:", [...this.selectedTasks]);
    },
  },
};
</script>

